import React, { useState, useEffect } from 'react';
import io from 'socket.io-client';
import { Line } from 'react-chartjs-2';
import { debounce } from 'lodash';
import { Chart, CategoryScale } from 'chart.js';
import * as Sentry from '@sentry/react'; // Import Sentry correctly

Chart.register(CategoryScale);

const logErrorToMyService = (error, errorInfo) => {
  Sentry.captureException(error, {
    extra: errorInfo,
  });
};

class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false };
  }

  static getDerivedStateFromError(error) {
    return { hasError: true };
  }

  componentDidCatch(error, errorInfo) {
    logErrorToMyService(error, errorInfo);
  }

  render() {
    if (this.state.hasError) {
      return <h1>Something went wrong.</h1>;
    }

    return this.props.children;
  }
}

const App = () => {
  const [data, setData] = useState({
    time: [],
    speed: [],
    soc: [],
  });

  useEffect(() => {
    const socket = io('http://localhost:8080/');
    socket.on('connect_error', (err) => {
      console.log('Connection error:', err);
    });
    socket.on('vehicleData', (data) => {
      try {
        if (data && data.time && data.speed && data.soc) {
          debouncedSetData(data);
        } else {
          console.log('Invalid data:', data);
        }
      } catch (err) {
        console.log('Error parsing data:', err);
      }
    });

    return () => {
      socket.disconnect();
    };
  }, []);

  const debouncedSetData = debounce((data) => {
    setData((prevData) => ({
      ...prevData,
      time: [...prevData.time, data.time],
      speed: [...prevData.speed, data.speed],
      soc: [...prevData.soc, data.soc],
    }));
  }, 100);

  const ChartData = ({ data }) => {
    const speedData = {
      labels: data.time.map((time) => new Date(time).toLocaleTimeString()),
      datasets: [
        {
          label: 'Speed (km/h)',
          data: data.speed,
          borderColor: 'rgba(54, 162, 235, 1)',
          borderWidth: 1,
        },
      ],
    };

    const socData = {
      labels: data.time.map((time) => new Date(time).toLocaleTimeString()),
      datasets: [
        {
          label: 'State of Charge (%)',
          data: data.soc,
          borderColor: 'rgba(255, 99, 132, 1)',
          borderWidth: 1,
        },
      ],
    };

    return (
      <div>
        <h2>Speed Profile</h2>
        <Line data={speedData} />
        <h2>State of Charge Profile</h2>
        <Line data={socData} />
      </div>
    );
  };

  return (
    <ErrorBoundary>
      <div>
        <h1>Vehicle Data</h1>
        <div style={{ display: 'flex', justifyContent: 'space-around' }}>
          <div style={{ width: '45%' }}>
            <ChartData data={data} />
          </div>
        </div>
      </div>
    </ErrorBoundary>
  );
};

export default App;